

class Conditions extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      dats: []
    };
    
  }

  getApi() {
    console.log("getApi Called")
    fetch("https://cors-anywhere.herokuapp.com/https://api.darksky.net/forecast/***REMOVED***/37.334789,-121.888138", { headers: {
      'Access-Control-Allow-Origin':'*'
    }})
    .then(res => res.json())
    .then((result) => {
        this.setState({
          isLoaded: true,
          dats: result
        });
      },
      // Note: it's important to handle errors here
      // instead of a catch() block so that we don't swallow
      // exceptions from actual bugs in components.
      (error) => {
        this.setState({
          isLoaded: true,
          error
        });
        console.log(error);
      }
    )
  }

  componentDidMount() {
    this.getApi();
  }

  componentWillUnmount() {
  }

  render() {
    const { error, isLoaded, items } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return (<div>
        <h1>Loading...</h1>
      </div>);
    } else {
      return (
       <div>
       <p>The Latitude is {JSON.stringify(this.state.dats.latitude, null, 2)}.</p>
       <p>The Longitude is {JSON.stringify(this.state.dats.longitude, null, 2)}.</p>
       <p>The Time Zone is is {JSON.stringify(this.state.dats.timezone, null, 2)}.</p>
       <p>The Current Temperature is {JSON.stringify(this.state.dats.currently.temperature, null, 2)}.</p>
       
       </div>
      );
    }
  }
}
export default Conditions;


